<template>
    <div>
        <el-container>
            <el-aside width="auto">
                <el-menu
                        style="height: 100vh;width:200px"
                        :router="true"
                >
                    <el-submenu v-for="item in nav" :index="item.name">
                        <template #title>
                            {{item.meta.title}}
                        </template>
                        <el-menu-item v-for="item2 in item.children" :index="item2.name" :route="{name:item2.name}"  active-text-color="#ffd04b">
                            <span slot="title">{{item2.meta.title}}</span>
                        </el-menu-item>
                    </el-submenu>
<!--                        <el-submenu index="1" >-->
<!--                            <template  slot="title" active-text-color="#ffd04b">-->
<!--                                <span slot="title">系统管理</span>-->
<!--                            </template>-->
<!--                            <el-menu-item-group>-->
<!--                                <el-menu-item index="1-1" :route="{name:'password'}" active-text-color="#ffd04b">-->
<!--                                    <span slot="title">密码修改</span>-->
<!--                                </el-menu-item>-->
<!--                            </el-menu-item-group>-->
<!--                            <el-menu-item-group>-->
<!--                                <el-menu-item index="1-2"  active-text-color="#ffd04b">-->
<!--                                    <span slot="title">系统设置</span>-->
<!--                                </el-menu-item>-->
<!--                            </el-menu-item-group>-->
<!--                            <el-menu-item-group>-->
<!--                                <el-menu-item index="1-3" :route="{name:'manger'}" active-text-color="#ffd04b">-->
<!--                                    <span slot="title">账户管理</span>-->
<!--                                </el-menu-item>-->
<!--                            </el-menu-item-group>-->
<!--                        </el-submenu>-->

                </el-menu>
            </el-aside>
            <el-container>
                <el-header style=" border-bottom: 1px solid #ccc;">
                    <el-row style="height: 60px; line-height: 60px; text-align: center;">
                        <el-col :span="6">
                            <span>鲜果汇后台管理系统</span>
                        </el-col>
                        <el-col :span="4" :offset="6" >
                            <span>当前时间:{{nowTime}}</span>
                        </el-col>
                        <el-col :span="4">
                            <span>当前账号:{{user}}</span>
                        </el-col>
                        <el-col :span="4">
                            <el-button type="text" @click="layout" >退出登录</el-button>
                        </el-col>
                    </el-row>
                </el-header>
                <el-main style="height: calc(100vh - 60px); overflow: auto">
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import formatDate from "../utils/formatDate";
    export default {
        data() {
            return {
                nowTime:"",
                user:sessionStorage.getItem('user'),
                nav:[]
            }
        },
        methods: {
            layout(){
                sessionStorage.removeItem('user');
                sessionStorage.removeItem('role');
                this.$router.push({name:"login"})
                location.reload();
            }
        },
        created() {
            setInterval(()=>{
                let Ntime=Date.now();
                this.nowTime=formatDate(Ntime/1000);
            },1000)
        },
        mounted() {
            if(sessionStorage.getItem('role')==1){
                this.nav=this.$store.state.obj1.children
            }else{
                this.nav=this.$store.state.obj2.children
            }
        },
    }
</script>
